.task-container {
  background-color: #444;
  margin: 8px 0;
  padding: 15px 20px;
  display: flex;
  border-radius: 5px;
  justify-content: space-between;
  color: #eee;
  align-items: center;
}

.task-title {
  cursor: pointer;
}

.remove-task-button {
  background-color: #444;
  border: none;
  font-size: 16px;
  color: chartreuse;
  cursor: pointer;
}

.see-task-details-button {
  cursor: pointer;
  border: none;
  font-size: 16px;
  background-color: #444;
  color: chartreuse;
}

.edit-task-button {
  cursor: pointer;
  border: none;
  font-size: 16px;
  background-color: #444;
  color: chartreuse;
}

Link {
  color: chartreuse;
  font-size: 16px;
}
